"use client";
import { Button, Container, Paper, Stack, Title, Text, Group } from "@mantine/core";
import Link from "next/link";
import axios from "axios";

export default function Home() {
  const logout = async () => { await axios.get("/api/auth/logout"); location.reload(); };

  return (
    <Container size="sm" py="xl">
      <Paper withBorder shadow="sm" p="xl" radius="md">
        <Stack gap="sm">
          <Title order={2}>Web Ops</Title>
          <Text c="dimmed">Contoh dashboard internal.</Text>
          <Group>
            <Button component={Link} href="/cases">Buka Cases (Protected)</Button>
            <Button component={Link} href="/login">Login</Button>
            <Button onClick={logout} variant="outline">Logout</Button>
          </Group>
        </Stack>
      </Paper>
    </Container>
  );
}
